/* eslint-disable react/jsx-no-target-blank */

import {AsideMenuItemWithSub} from './AsideMenuItemWithSub'
import {AsideMenuItem} from './AsideMenuItem'
import {useIntl} from 'react-intl'

export function AsideMenuMain() {
  const intl = useIntl()
  const superAdmin = () => {
    if (1 > 0) {
      return (
        <AsideMenuItemWithSub
          to='/admin/settings'
          title='Settings'
          fontIcon='bi-chat-left'
          icon='/media/icons/duotune/abstract/setting.svg'
        >
          <AsideMenuItem to='/admin/settings/provinces' title='Tnh/Thnh ph' />
          <AsideMenuItem to='/admin/settings/districts' title='Qun/Huyn/Th x' />
          <AsideMenuItem to='/admin/settings/sub-districts' title='Phng/X' />
          <AsideMenuItem to='/admin/settings/settingWebHook' title='WebHook' />
        <AsideMenuItem to='/admin/settings/customCSS' title='Custom CSS' />
        <AsideMenuItem to='/admin/settings/settingSMTP' title='SMTP' />
        </AsideMenuItemWithSub>
      )
    } else {
      return <></>
    }
  }
  return (
    <>
      {/* qun l ti khon */}
      <div className='aside_pt'></div>
      <AsideMenuItemWithSub
        to='/crafted/pages'
        title={intl.formatMessage({id: 'MENU.DASHBOARD'})}
        fontIcon='bi-archive'
        icon='/media/icons/duotune/general/quanlytaikhoan.svg'
      >
        <AsideMenuItem to='/crafted/pages/profile/overview' title='i tc' hasBullet={true} />
        <AsideMenuItem to='/crafted/pages/profile/projects' title='Khch hng' hasBullet={true} />
      </AsideMenuItemWithSub>
      {/* qun l bi ng */}
      <AsideMenuItem
        to='/crafted/widgets/managerfeed'
        title='Qun l bi ng'
        fontIcon='bi-person'
        icon='/media/icons/duotune/general/quanlybaidang.svg'
      />
      {/* xp hng- bo co */}
      <AsideMenuItem
        to='/crafted/widgets/statistics'
        title='Xp hng - Bo co'
        fontIcon='bi-sticky'
        icon='/media/icons/duotune/general/xephangbaocao.svg'
      />
      {/* Qun l n t */}
      <AsideMenuItem
        to='/crafted/widgets/managerorder'
        title='Qun l n t'
        icon='/media/icons/duotune/general/quanlydondat.svg'
        fontIcon='bi-layers'
      />
      {/* Data Export */}

      <AsideMenuItem
        to='/crafted/widgets/mixed'
        title='Data Export'
        fontIcon='bi-chat-left'
        icon='/media/icons/duotune/general/dataexport.svg'
      />
      {/* Chnh sch */}

      <AsideMenuItem
        to='/crafted/widgets/tables'
        title='Chnh sch'
        fontIcon='bi-chat-left'
        icon='/media/icons/duotune/general/chinhsach.svg'
      />
      {/* Do */}

      <AsideMenuItem
        to='/crafted/widgets/dao'
        title='Do'
        fontIcon='bi-chat-left'
        icon='/media/icons/duotune/general/dao.svg'
      />
      {/* Thng bo */}
      <AsideMenuItemWithSub
        to='/apps/chat'
        title='Thng bo'
        fontIcon='bi-chat-left'
        icon='/media/icons/duotune/general/thongbao.svg'
      >
        <AsideMenuItem to='/apps/chat/partner' title='i tc' />
        <AsideMenuItem to='/apps/chat/user' title='Khch hng' />
        <AsideMenuItem  to='/apps/chat/edit' title='To thng bo' />
        <AsideMenuItem to='/apps/chat/setting' title='Notification Setting' />
      </AsideMenuItemWithSub>
      {/* setting */}
      {superAdmin()}
      
    </>
  )
}
